:root {
  --link-color: #00acdc;
  --text-color: #000;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.list {
  text-transform: uppercase;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.elements {
  margin: 0 10px;
  position: relative;
}

.link {
  text-align: center;
  color: var(--text-color);
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.elements:first-child {
  margin-left: 0;
}

.elements:last-child {
  margin-right: 0;
}

.link:hover, .link.is-active {
  color: var(--link-color);
}

.link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
}

